.cart {
    padding-top: 120px;
    padding-bottom: 50px;
    min-height: 100vh;
    &__title {
        font-size: 36px;
        color: #fff;
        text-align: center;
        font-weight: 300;
    }
    &__list {
        width: 60%;
        margin: 0 auto;
    }
    &__item {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 40px;
        border: 1px solid rgba(256,256,256, .2);
        background-color: rgba(0,0,0,.7);
        box-shadow: 0 0 20px rgba(256,256,256, .1);
        &-title, &-price  {
            width: 250px;
            text-align: center;
            color: #fff;
            font-size: 24px;
        }
        &-img {
            height: 125px;
            width: 250px;
            object-fit: cover;
        }
    }
    &__close {
        position: absolute;
        width: 25px;
        height: 25px;
        text-align: center;
        top: -10px;
        right: -10px;
        color: #fff;
        font-size: 20px;
        border-radius: 100%;
        border: 1px solid #fff;
        cursor: pointer;
    }
    &__order {
        display: flex;
        font-size: 18px;
        margin-top: 70px;
        padding: 5px 0;
        background-color: black;
    }
    &__quantity {
        padding-left: 10px;
        color: green;
    }
    &__counter {
        color: darkred;
        padding: 0 15px;
    }
    &__btn-order {
        display: block;
        color: white;
        background-color: #14a114;
        font-size: 16px;
        text-align: center;
        padding: 10px;
        margin: 50px auto;
        border-radius: 5px;
        border: 0;
        text-decoration: none;
        cursor: pointer;
        user-select: none;
        outline: 0;
    }
}